@charset "UTF-8";
@namespace svg url("http://www.w3.org/2000/svg");

/* Full Reset */
:not(svg|*) {
  all: unset;
  display: revert;
  box-sizing: border-box;
}

[default-styles] {
  all: revert;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* html */
html {
  background: #f5f5f5;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "liga" 0;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: #333;
  scroll-behavior: smooth;
}

html,
body {
  display: block;
  width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 16px;
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
}

html:focus,
body:focus {
  outline: none;
}

/* Game Assets */
section {
  width: 100vw;
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#game {
  background: #0a58ca;
  z-index: 0;
}

#game::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 50vh;
  background: #0d6efd;
  z-index: 1;
}

#game .status {
  position: absolute;
  font-size: 20vw;
  color: #0a58ca;
  text-align: center;
  width: 100vw;
  height: 24vw;
  transform-origin: center bottom;
  transform: translateY(-50vw);
  font-weight: 800;
  z-index: 20;
}

#game .status::after {
  position: absolute;
  content: "Last guess:";
  color: #c5c5c5;
  font-size: 1rem;
  left: 0;
  width: 100%;
  text-align: center;
}

#game[result=""] .status,
#game[result="winner"] .status {
  color: white;
}

#game[result=""] .status::after,
#game[result="init"] .status::after,
#game[result="winner"] .status::after {
  content: "";
}

.guess-holder {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 76vw;
  height: 76vw;
  position: relative;
  border-radius: 40vw;
  border: 1px solid transparent;
  background: #0a58ca;
  z-index: 2;
}

.guess-holder::before {
  content: "";
  position: absolute;
  width: 76vw;
  height: 38vw;
  border-radius: 38vw 38vw 0 0;
  background: white;
  mix-blend-mode: overlay;
  transform-origin: center bottom;
  transform: rotate(180deg);
  transition: 3s ease-in-out;
  top: 0;
  z-index: 3;
}

.guess-holder::after {
  content: "";
  position: absolute;
  width: calc(76vw + 2rem);
  height: calc(76vw + 2rem);
  left: -1rem;
  top: -1rem;
  border-radius: calc(38vw + 1rem);
  border: 1rem solid rgba(21, 21, 21, 0.6);
  background: transparent;
  mix-blend-mode: multiply;
  z-index: 4;
}

#game[result="winner"] .guess-holder::before {
  transition-duration: 1s;
  transition-timing-function: ease-out;
  transform: rotate(360deg);
}

#game[result="higher"] .guess-holder::before {
  transform: rotate(225deg);
}

#game[result="lower"] .guess-holder::before {
  transform: rotate(135deg);
}

.guess-holder input[type="number"] {
  font-size: 36vw;
  font-weight: 700;
  letter-spacing: -0.4rem;
  color: #f5f5f5;
  text-align: center;
  line-height: 1;
  z-index: 10;
}

.guess-holder input[type="number"]::placeholder {
  color: #c5c5c5;
}

.attempts {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  font-size: 14vw;
  font-weight: 300;
  text-align: center;
  line-height: 1;
  height: 100vh;
  width: auto;
  position: absolute;
  overflow: hidden;
  padding: 1rem 0;
  color: white;
  top: 0;
  right: 0;
  z-index: 8;
}

.attempts small,
.attempts span {
  display: block;
  width: 100%;
  padding: 0.25rem 0.5rem;
  text-align: center;
}

.attempts small {
  font-size: 0.72rem;
}

.attempts ul {
  list-style: none;
  width: 8vw;
  display: block;
}

.attempts li {
  display: block;
  width: 8vw;
  height: 1vh;
  margin: 0.5vh 0;
  background: white;
  mix-blend-mode: overlay;
  opacity: 0.6;
}

section > button:not(#reset) {
  position: absolute;
  padding: 1rem;
  bottom: 0;
  z-index: 25;
}

button,
a {
  cursor: pointer;
}

a {
  font-weight: 600;
  color: #0a58ca;
  letter-spacing: -0.2px;
}

#toConfig svg {
  fill: white;
}

#toConfig:hover svg {
  fill: #ffc107;
}

#toGame svg {
  fill: #666666;
}

#toGame button:hover svg {
  fill: #1a1a1a;
}

#game .info {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  padding: 1rem;
  font-weight: 300;
  width: 50%;
  color: white;
  z-index: 30;
}

#params {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 90%;
  align-items: center;
}

#params .choose {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 1rem;
  font-weight: 200;
  padding: 1rem 10%;
}

fieldset {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 1rem;
  max-width: 50%;
}

#params label {
  display: block;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 300;
}

#params input {
  display: block;
  text-align: center;
  font-size: 4.75rem;
  font-weight: 300;
  border-bottom: 1px solid #333;
  max-width: 100%;
}

strong {
  font-weight: 600;
}

main[current="config"] > #game,
main[current="game"] > #config {
  display: none;
}

main[current="game"] > #game,
main[current="config"] > #config {
  display: flex;
}

#reset {
  width: 80%;
  background: #333333;
  color: #f5f5f5;
  font-size: 1.5rem;
  text-align: center;
  line-height: 1;
  padding: 1rem;
  margin: 1.5rem 0;
}

.hidden {
  display: none;
}

h1 {
  font-size: 7.5rem;
  font-weight: 400;
  text-align: left;
  display: block;
  width: 80%;
  line-height: 0.9;
  letter-spacing: -0.25rem;
  padding-bottom: 1.5rem;
}

h1 > small {
  font-size: 1.63rem;
  display: block;
  padding-left: 0.4rem;
  letter-spacing: -0.05rem;
  font-weight: 200;
}

#game input::-webkit-outer-spin-button,
#game input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

#game input[type="number"] {
  -moz-appearance: textfield;
}
#game input[type="number"]:disabled {
  color: #f5f5f5;
}

@media (min-width: 768px) {
  section > * {
    max-width: 768px;
  }

  .guess-holder {
    width: 640px;
    height: 640px;
    border-radius: 380px;
  }

  .guess-holder::before {
    width: 640px;
    height: 320px;
    border-radius: 336px 336px 0 0;
  }

  .guess-holder::after {
    width: calc(640px + 2rem);
    height: calc(640px + 2rem);
    border-radius: calc(320px + 1rem);
  }

  .guess-holder input[type="number"] {
    font-size: 360px;
  }

  #game .info {
    left: calc(50% - 480px);
  }

  #game .status {
    width: 768px;
    height: 224px;
    font-size: 160px;
    transform: translateY(-360px);
    z-index: 1;
  }
  #game[result="winner"] .status {
    z-index: 20;
    transform: translateY(-284px);
  }

  .attempts {
    right: calc(50% - 480px);
    font-size: 140px;
  }

  .attempts small {
    font-size: 1rem;
  }
}
